* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  height: 100%;
  overflow-x: hidden;
  overflow-y: scroll;
}

body {
  color: #fff;
  min-height: 100vh;
  background: url("../images/bg-intro-mobile.c9a2f283.png"), #ff7a7a;
  padding-top: 9.5rem;
  font-family: Poppins, sans-serif;
}

.error-message, .form__wrapper form .input__wrapper.error span.error {
  width: 100%;
  color: #ff7a7a;
  text-align: right;
  margin-bottom: 1rem;
  font-size: .675rem;
  font-style: italic;
  font-weight: 600;
  display: block;
}

span.error {
  opacity: 0;
}

section.introductory__msg {
  text-align: center;
}

section.introductory__msg p {
  margin-top: 2.8rem;
  margin-bottom: 5.1rem;
  font-size: 1rem;
}

.form__wrapper {
  margin-bottom: 4.0625rem;
  padding: 1.5rem .875rem;
}

@media only screen and (min-width: 520px) {
  .form__wrapper {
    padding: 2.5rem;
  }
}

.form__wrapper .try__it {
  text-align: center;
  background: #6055a5;
  border-radius: .5rem;
  margin-bottom: 1.75rem;
  padding: 1.5rem 0;
  box-shadow: 0 5px #3e3c4947;
}

.form__wrapper .try__it p {
  font-size: 1rem;
}

.form__wrapper form {
  background: #fff;
  border-radius: .5rem;
  padding: 1.5rem 1.5625rem;
  box-shadow: 0 5px #3e3c4947;
}

.form__wrapper form .input__wrapper {
  position: relative;
}

.form__wrapper form .input__wrapper.error:before {
  content: url("../images/icon-error.52aa9fe2.svg");
  position: absolute;
  top: 20%;
  bottom: 35%;
  right: 3%;
}

.form__wrapper form .input__wrapper.error span.error {
  content: "* cannot be error";
  opacity: 1;
}

.form__wrapper form .input__wrapper input[type="text"], .form__wrapper form .input__wrapper input[type="email"], .form__wrapper form .input__wrapper input[type="password"] {
  width: 100%;
  border: 1px solid #b9b6d3;
  border-radius: .5rem;
  margin-bottom: .5rem;
  padding: 1.375rem 1.25rem;
  font-family: Poppins, sans-serif;
  font-size: .875rem;
  font-weight: 600;
}

.form__wrapper form .input__wrapper input[type="text"]:focus, .form__wrapper form .input__wrapper input[type="email"]:focus, .form__wrapper form .input__wrapper input[type="password"]:focus {
  border-color: #38cc8c;
}

.form__wrapper form input[type="submit"] {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background: #38cc8c;
  border: none;
  border-radius: .5rem;
  margin-bottom: 1.125rem;
  padding: 1.375rem 0;
  font-family: Poppins, sans-serif;
  font-weight: 600;
  box-shadow: 0 3px #299e6bf2;
}

.form__wrapper form input[type="submit"]:hover {
  background: #5dd5a1;
}

.form__wrapper form p {
  color: #b9b6d3;
  text-align: center;
  font-size: .825rem;
}

.form__wrapper form p span.terms {
  color: #ff7a7a;
  cursor: pointer;
  font-size: .725rem;
  font-weight: 700;
}

@media only screen and (min-width: 52.8125rem) {
  body {
    background: url("../images/bg-intro-desktop.59b8b476.png"), #ff7a7a;
    padding-top: 0;
  }

  main {
    height: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  main section.introductory__msg {
    text-align: left;
    width: 50%;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 5vh;
    padding-left: 8vw;
    padding-right: 5vw;
    display: flex;
  }

  main section.introductory__msg h1 {
    font-size: 3rem;
  }

  main section.introductory__msg p {
    margin-top: 2rem;
    font-size: .925rem;
    font-weight: 500;
  }

  main .form__wrapper {
    width: 50%;
    height: 100vh;
    margin-bottom: 0;
    padding-right: 8vw;
  }

  footer {
    margin-top: 4.0625rem;
  }

  footer .attribution {
    text-align: center;
    color: #fff;
    margin-bottom: .5rem;
    font-size: .875rem;
  }

  footer .attribution a {
    color: #6055a5;
  }
}

